<template>
  <el-row :gutter="20">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 50px;">
        <a href="https://github.com/zhufuyi/sponge">sponge</a>
        是一个go语言微服务框架，也是一个快速创建web服务和微服务工具，sponge拥有丰富的生成代码功能，自动生成常用的重复代码和脚本，很轻松就搭建起来完整的服务端工程代码，节省不少的工作量，下面是生成代码框架图：
      </p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  /* background-color:brown; */
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}
</style>



